.buttonImport{
    font-family: "Ubuntu";
    font-weight: 300;
    font-size: 26px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-style: solid;
    border-width: 2px;
    border-radius: 2px;
    border-color: #799166;
    min-height: 52px;
    min-width: 200px;
    background-color: #92C47D;
    color:#E9FFd8;
}
.buttonContainer{
    float: right;
    margin-top: 110px;
    margin-right: 10px;
    display: inline-flex;
    flex-flow: row wrap;
}